import request from '@/utils/request'
import { getStorage } from '@/utils/storage'

// 登录
export const reqLogin = ({ mobile, code }) =>
  request({ url: '/v1_0/authorizations', method: 'post', data: { mobile, code } })

// 刷新token
export const reqRefreshToken = () =>
  request({
    url: '/v1_0/authorizations',
    method: 'put',
    headers: {
      Authorization: 'Bearer ' + getStorage('refresh_token')
    }
  })

// 请求所有频道列表
export const reqAllChannelList = () => request.get('/v1_0/channels')

// 请求用户选择的频道
// 需要在headers携带token
// 未登录则返回默认的列表
export const reqUserChannelList = () => request.get('/v1_0/user/channels')

// 设置频道
export const reqSetChannels = channels => request({ url: '/v1_0/user/channels', method: 'put', data: { channels } })

// 获取文章列表
export const reqArticleList = ({ channel_id, timestamp }) =>
  request({ url: '/v1_0/articles', method: 'get', params: { channel_id, timestamp } })

// 文章 - 反馈 - 不感兴趣
export const reqDislike = id => request({ url: '/v1_0/article/dislikes', method: 'post', data: { target: id } })

// 文章 - 反馈 - 反馈垃圾内容
export const reqReports = (id, type) =>
  request({ url: '/v1_0/article/reports', method: 'post', data: { target: id, type, remark: '' } })

// 搜索模块

// 根据搜索关键词获取搜索建议
export const reqSuggestList = keyword => request.get('/v1_0/suggestion', { params: { q: keyword } })

// 搜索结果
export const reqSearchResult = ({ page, per_page, keyword }) =>
  request.get('/v1_0/search', { params: { page, per_page, q: keyword } })

// 文章详情页

// 通过id获取文章详情
export const reqArtDetail = art_id => request.get(`/v1_0/articles/${art_id}`)

// 关注作者
export const reqFollow = aut_id => request.post('/v1_0/user/followings', { target: aut_id })

// 取关作者
export const reqCancelFollow = aut_id => request({ url: `/v1_0/user/followings/${aut_id}`, method: 'delete' })

// 文章点赞
export const reqLike = art_id => request({ url: '/v1_0/article/likings', method: 'post', data: { target: art_id } })

// 取消点赞
export const reqCancelLike = aut_id => request({ url: `/v1_0/article/likings/${aut_id}`, method: 'delete' })

// 收藏文章
export const reqCollect = art_id =>
  request({ url: '/v1_0/article/collections', method: 'post', data: { target: art_id } })

// 取消收藏文章 /v1_0/article/collections/{target}
export const reqCancelCollect = art_id => request({ url: `/v1_0/article/collections/${art_id}`, method: 'delete' })

// 文章评论相关

// 获取文章评论列表
// type: 'a' - 文章的评论列表; 'c' - 评论的回复列表
// source: 文章或评论的id
// offset 和 limit 用于分页加载, 其中offset值为 评论的id
export const reqCmtList = ({ type = 'a', id, offset = null, limit = 10 }) =>
  request.get('/v1_0/comments', { params: { type, source: id, offset, limit } })

// 点赞评论
export const reqLikeCmt = id => request({ url: '/v1_0/comment/likings', method: 'post', data: { target: id } })

// 取消点赞评论
export const reqCancelLikeCmt = id => request({ url: `/v1_0/comment/likings/${id}`, method: 'delete' })

// 发布评论
export const reqPublishCmt = ({ id, content }) =>
  request({ url: '/v1_0/comments', method: 'post', data: { target: id, content } })

// 用户相关

// 获取用户基本资料
export const reqUserBaseInfo = () => request.get('/v1_0/user')

// 资料编辑页 - 获取用户个人简介
export const reqUserIntro = () => request.get('/v1_0/user/profile')

// 修改用户信息
export const reqEdit = ({ name = null, birthday = null, gender }) => {
  const obj = {
    gender
  }
  if (name) obj.name = name
  if (birthday) obj.birthday = birthday
  if (birthday) obj.birthday = birthday

  return request({ url: '/v1_0/user/profile', method: 'patch', data: obj })
}

// 修改头像
// 注意: formObj的值必须是一个表单对象
// '{"a": 10, "b": 20}' // 对象格式的JSON字符串
// new FormData() // 表单对象
// 如果你的请求体内容是表单对象, 浏览器会自动携带请求头Content-Type为multipart/form-data
export const reqUpdateAvatar = formObj => request({ url: '/v1_0/user/photo', method: 'patch', data: formObj })
