import ajax from 'axios'
import { getToken, setToken, removeToken } from '@/utils/token'
import { reqRefreshToken } from '@/api'
import { Notify } from 'vant'
import router from '@/router'

const axios = ajax.create({
  baseURL: 'http://geek.itheima.net/',
  timeout: 20000
})

// 请求拦截器
axios.interceptors.request.use(
  config => {
    const token = getToken()
    // ?. 可选链操作符, 如果前面的对象没有length, 则原地返回undefined; 有值才能调用length获取长度
    // token?.length > 0 等价于 token && token.length > 0
    if (token?.length > 0 && config.headers.Authorization === undefined) {
      config.headers.Authorization = `Bearer ${token}`
    }
    return config
  },
  err => {
    return Promise.reject(err)
  }
)

// 响应拦截器
axios.interceptors.response.use(
  response => {
    // http响应代码2xx 3xx进入这里
    return response
  },
  async err => {
    // http响应代码4xx 5xx进入这里
    if (err.response.status === 401) {
      // 身份过期

      // 方式一: 用户有感
      // removeToken()
      // Notify({ type: 'warning', message: '身份验证失败,请重新登录' })
      // // 不能使用this.$router, 因为this为undefined
      // // 需要引入router
      // router.replace('/login')

      // 方式二: 用户无感, 效果好
      const res = await reqRefreshToken()
      // 1. 更新本地token
      setToken(res.data.data.token)
      // 2. 更新新的token在请求头里
      err.config.headers.Authorization = `Bearer ${res.data.data.token}`
      // 3. 由于token过期未完成的请求,重新发起
      // err.config 就是上一次请求的配置对象
      return axios(err.config)
    } else if (
      err.response.status === 500 &&
      err.config.url === '/v1_0/authorizations' &&
      err.config.method === 'put'
    ) {
      // refresh_token 也过期了
      // 只能清除token, 重新登录
      removeToken()
      Notify({ type: 'warning', message: '身份验证失败,请重新登录' })
      // 不能使用this.$router, 因为this为undefined
      // 需要引入router
      // 重新登录后跳转到刚才的页面
      // router.currentRoute 相当于Vue组件内的this.$route
      router.replace(`/login?path=${router.currentRoute.fullPath}`)
    }
    return Promise.reject(err)
  }
)

export default axios
