// 此模块用于按需引入vant组件
import Vue from 'vue'
// 引入vant组件
import {
  NavBar,
  Form,
  Field,
  Button,
  Tabbar,
  TabbarItem,
  Icon,
  Tab,
  Tabs,
  Cell,
  List,
  PullRefresh,
  ActionSheet,
  Popup,
  Col,
  Row,
  Badge,
  Search,
  Divider,
  Loading,
  Dialog,
  Picker,
  DatetimePicker,
  Lazyload
} from 'vant'

// 注册vant组件
Vue.use(NavBar)
Vue.use(Form)
Vue.use(Field)
Vue.use(Button)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(Icon)
Vue.use(Tab)
Vue.use(Tabs)
Vue.use(Cell)
Vue.use(List)
Vue.use(PullRefresh)
Vue.use(ActionSheet)
Vue.use(Popup)
Vue.use(Col)
Vue.use(Row)
Vue.use(Badge)
Vue.use(Search)
Vue.use(Divider)
Vue.use(Loading)
Vue.use(Dialog)
Vue.use(Picker)
Vue.use(DatetimePicker)
Vue.use(Lazyload, {
  error: 'https://img95.699pic.com/element/40184/3399.png_300.png'
})
