// 生产环境去除打印语句
import '@/utils/console'
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 引入flexible.js, 用于移动端适配
import 'amfe-flexible'
// 引入vant.js
import '@/utils/vant'
import directiveObj from './utils/directive'

import 'highlight.js/styles/default.css' // 代码高亮的样式

// 将全部api接口函数注册到Vue.prototype.$API
import * as API from '@/api'
Vue.prototype.$API = API

Vue.config.productionTip = false

// Vue.use() 命令: 执行传入的对象身上的install方法, 并将Vue传进去
Vue.use(directiveObj)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

// 大数字丢精度问题
// Number.MAX_SAFE_INTEGER : 9007199254740991
// Number.MIN_SAFE_INTEGER : -9007199254740991
const str = '[{"id": 1302900300041101987}, {"id": 1205340366642205763}, {"id": 7689021398237123422}]'
const arr = JSON.parse(str)
console.log(arr) // 末尾4位数字不能精确显示
// 需要安装第三方包json-bigint
// storeAsString: true 将大数字转换成字符串存储, 不加这项则会分割成小数存在对象里
const jsonBig = require('json-bigint')({ storeAsString: true })
console.log(jsonBig.parse(str))
