import Vue from 'vue'
import VueRouter from 'vue-router'
import { getToken } from '@/utils/token'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/layout/home'
  },
  {
    path: '/login',
    component: () => import(/* webpackChunkName: "Login" */ '@/views/Login'),
    beforeEnter: (to, from, next) => {
      // 登录状态不能进入
      const token = getToken()
      if (token) return next('/')
      next()
    }
  },
  {
    path: '/layout',
    component: () => import(/* webpackChunkName: "Layout" */ '@/views/Layout'),
    children: [
      {
        path: 'home',
        component: () => import(/* webpackChunkName: "Home" */ '@/views/Layout/Home'),
        meta: { scrollT: 0 } // 用于保存滚动条的位置
      },
      {
        path: 'user',
        component: () => import(/* webpackChunkName: "User" */ '@/views/Layout/User')
      }
    ]
  },
  {
    path: '/search',
    component: () => import(/* webpackChunkName: "Search" */ '@/views/Search')
  },
  {
    path: '/search/:keyword',
    component: () => import(/* webpackChunkName: "SearchResult" */ '@/views/Search/SearchResult')
  },
  {
    path: '/article_detail',
    component: () => import(/* webpackChunkName: "ArticleDetail" */ '@/views/ArticleDetail')
  },
  {
    path: '/user_editor',
    component: () => import(/* webpackChunkName: "UserEditor" */ '@/views/Layout/User/UserEditor')
  },
  {
    path: '/chat',
    component: () => import(/* webpackChunkName: "Chat" */ '@/views/Layout/User/Chat')
  }
]

const router = new VueRouter({
  routes
})

// 全局前置守卫
const arr = ['layout/user']
router.beforeEach((to, from, next) => {
  next()
})

export default router
