import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    avatar: '' // 用户头像
  },
  actions: {},
  mutations: {
    GET_AVATAR(state, photo) {
      state.avatar = photo
    }
  },
  getters: {},
  modules: {}
})

export default store
