const directiveObj = {
  install(Vue) {
    // 自定义全局指令
    Vue.directive('focus', {
      // inserted 只在标签插入到真实DOM时触发, 用display:none隐藏再出现不会触发,需要使用update
      inserted(el) {
        if (el.nodeName === 'INPUT' || el.nodeName === 'TEXTAREA') {
          el.focus()
        } else {
          // 如果绑定的是组件标签,则el得到的是组件的原生dom
          // input在其内部
          const theInput = el.querySelector('input')
          const theTextarea = el.querySelector('textarea')
          theInput && theInput.focus()
          theTextarea && theTextarea.focus()
        }
      },
      update(el) {
        if (el.nodeName === 'INPUT' || el.nodeName === 'TEXTAREA') {
          el.focus()
        } else {
          // 如果绑定的是组件标签,则el得到的是组件的原生dom
          // input在其内部
          const theInput = el.querySelector('input')
          const theTextarea = el.querySelector('textarea')
          theInput && theInput.focus()
          theTextarea && theTextarea.focus()
        }
      }
    })
  }
}

export default directiveObj
